<template>
	<div class="stratistics">
		<div class="page-aside-section">
			<template v-for="(item,ind) in navList">
				<h5 class="page-aside-title" :key="ind">{{item.title}}</h5>
				<ul class="list-group" :key="item.title">
					<li v-for="(i,index) in item.li" :key="index" @click="listBtn(ind,index)">
						<a :class="ind == navIndex[0]? index == navIndex[1] ? 'active':'' : '' " href="javascript:00">{{i.title}}</a>
					</li>
				</ul>
			</template>
		</div>
		<div class="dataShow">
			<div class="dataShow-selsect">

			</div>
			<div id="myChart" style="width: 600px;height:400px;"></div>
			<div class="">
				<table class="table" border="" cellspacing="" cellpadding="">
					<thead>
						<tr>
							<th v-for="(item,index) in navList[navIndex[0]].li[navIndex[1]].th" :key="index">{{item}}</th>
						</tr>
					</thead>
					<tbody>

					</tbody>
					<tfoot>
						<tr>
							<td>合计</td>
							<td></td>
							<td>1</td>
						</tr>
					</tfoot>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				navList: [{
					title: "客户统计",
					li: [{
							title: "创建客户统计",
							th: ['编号', '日期', '客户总数']
						},
						{
							title: "创建客户统计(员工维度)",
							th: ['编号', '员工', '客户总数']
						}, {
							title: "联系客户统计",
							th: ['编号', '日期', '客户总数']
						}, {
							title: "联系客户统计(员工维度)",
							th: ['编号', '员工', '客户总数']
						}, {
							title: "成交客户统计",
							th: ['编号', '日期', '客户总数']
						}, {
							title: "成交客户统计(员工维度)",
							th: ['编号', '员工', '客户总数']
						}, {
							title: "客户分类统计",
							th: ['编号', '客户分类', '客户数量']
						}, {
							title: "客户标签统计",
							th: ['编号', '客户标签', '客户数量']
						}, {
							title: "自定义字段统计",
							th: ['编号', '分类', '客户数量']
						}, {
							title: "客户数量统计",
							th: ['编号', '员工', '已联系客户数', '未联系客户数', '合计']
						}
					]
				}, {
					title: "合同统计",
					li: [{
						title: "合同统计",
						th: ['编号', '日期', '数量', '金额(元)']
					}, {
						title: "续约统计(员工维度)",
						th: ['编号', '状态', '数量', '金额(元)']
					}, {
						title: "合同自定义字段统计",
						th: ['编号', '分类', '数量', '金额(元)']
					}]
				}, {
					title: "款项统计",
					li: [{
						title: "款项统计",
						th: ['编号', '日期', '数量', '金额(元)']
					}, {
						title: "票据统计",
						th: ['编号', '日期', '数量', '金额(元)']
					}]
				}],
				navIndex: [0, 0],
				//列表点击指针
			}
		},
		methods: {
			listBtn: function(ind, index) { //列表点击
				this.navIndex.splice(0, 1, ind);
				this.navIndex.splice(1, 1, index);

			},
			drawLine() { //统计图数据
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				var option = {
					toolbox: {
						feature: {
							saveAsImage: {
								show: true
							}
						}
					},
					xAxis: {
						type: 'category',
						data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
					},
					yAxis: {
						type: 'value'
					},
					series: [{
						data: [120, 200, 150, 80, 70, 110, 130],
						type: 'bar'
					}]
				};
				myChart.setOption(option);
			}
		},
		mounted() {
			this.drawLine();
		},
	}
</script>

<style scoped>
	.stratistics {
		display: flex;
		margin: 20px;
		padding: 20px;
	}

	.page-aside-section {
		width: 250px;
		border-right: solid 1px #ccc;
	}

	.page-aside-title {
		padding: 10px 30px;
		margin: 20px 0 10px;
		overflow: hidden;
		font-size: 18px;
		color: #616161;
		text-overflow: ellipsis;
		white-space: nowrap;
		cursor: default;
	}

	.list-group {
		padding-left: 0;
		margin-bottom: 20px;
	}

	.list-group li {
		width: 100%;
	}

	.list-group li a {
		width: 100%;
		display: inline-block;
		padding: 13px 30px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		border: 0;
		color: #555;
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;
	}

	.list-group li .active {
		color: #fff;
		background-color: #3f51b5;
	}

	.dataShow {
		padding: 20px 0 0 40px;
		flex: 1;
	}

	.table {
		color: #333;
		border: 1px solid #e1e6eb;
		width: 100%;
		max-width: 100%;
		margin: 20px 0;
	}
</style>
